import React, { Component } from 'react'
import '../../node_modules/react-vis/dist/style.css'
import {
  XYPlot,
  LineSeries,
  VerticalGridLines,
  HorizontalGridLines,
  XAxis,
  YAxis,
  curveCatmullRom
} from 'react-vis'

class ChartsContainer extends Component {
  render () {
    const data = [
      { x: 0, y: 8 },
      { x: 1, y: 5 },
      { x: 2, y: 4 },
      { x: 3, y: 9 },
      { x: 4, y: 1 },
      { x: 5, y: 7 },
      { x: 6, y: 6 },
      { x: 7, y: 3 },
      { x: 8, y: 2 },
      { x: 9, y: 0 }
    ]
    return (
      <div className='App'>
        <XYPlot width={300} height={300}>
          <HorizontalGridLines />
          <VerticalGridLines />
          <XAxis title='X Axis' position='start' />
          <YAxis title='Y Axis' />
          <LineSeries
            className='first-series'
            data={[
              { x: 1, y: 3 },
              { x: 2, y: 5 },
              { x: 3, y: 15 },
              { x: 4, y: 12 }
            ]}
          />
          <LineSeries className='second-series' data={null} />
          <LineSeries
            className='third-series'
            curve={'curveMonotoneX'}
            style={{
              strokeDasharray: '2 2'
            }}
            data={[
              { x: 1, y: 10 },
              { x: 2, y: 4 },
              { x: 3, y: 2 },
              { x: 4, y: 15 }
            ]}
            strokeDasharray='7, 3'
          />
          <LineSeries
            className='fourth-series'
            curve={curveCatmullRom.alpha(0.5)}
            data={[
              { x: 1, y: 7 },
              { x: 2, y: 11 },
              { x: 3, y: 9 },
              { x: 4, y: 2 }
            ]}
          />
        </XYPlot>
      </div>
    )
  }
}

export default ChartsContainer



